<template>
  <div class="river-house">
    <!-- Q10 -->
    <div v-if="step===10" class="house-0-box">
      <div class="house-0-all-245-box">
       <div class="house-0-245-text">眼前的怪奇似乎心理上遭受莫大的打擊，它的形體突然化為六塊碎片，往不同方向噴射出去。看來如果要收服他，就得找回這六塊碎片。<span style="color: #95ff00;">請先前往「曙・初見咖啡」，到了之後</span>拿出道具包的「手冊」以及「填字卡」，<span style="color: #95ff00;">填完內容後，再向店員索取「現形片」。</span>透過填字卡及現形片，你將得知怪奇的名字。</div>
       <div>怪奇的名字</div>
        <input v-model="riverName" class="house-0-245-input" type="text">
        <div @click="answerRiverName" class="house-0-245-go">GO</div>
      </div>
    </div>
    <!-- Q10.5 -->
    <div v-if="step===10.5" class="house-0-box">
      <div class="house-0-all-245-box">
       <div class="house-0-245-text">你知曉了怪奇的名字，但他的人格碎片仍四散在各處。請疊合現形片及填字卡，<span style="color: #95ff00;">以「虛線內的污點」為線索</span>，前往大樓的各處蒐集人格碎片，查看碎片的內容，並按照時間順序填入碎片代碼。</div>
       <div>代碼順序</div>
        <input v-model="peaceName" class="house-0-245-input" type="text">
        <div @click="answerPeaceName" class="house-0-245-go">GO</div>
      </div>
    </div>
    <!-- Q11 -->
    <div @click="goToStepQ111" v-if="step===11" class="house-0-box" :style="{ backgroundImage: 'url(' + silverBg + ')' }">
      <div class="house-0-talk-box">
        <div class="house-0-float-frame">
          <img class="house-0-float-man" src="@/assets/images/river/river-peace-icon.png" alt="ghost">
          <div class="house-0-float-text">你成功蒐集完怪奇的人格碎片，也得知了他的名字：旭川河。<br>經過你重新拼湊後的他，重新在你眼前顯現，而且形象、氣場都有所轉變，甚至散發出一種神聖的光芒。</div>
        </div>
      </div>
    </div>
    <!-- Q11-1 -->
    <div v-if="step===11.1" class="house-0-box" :style="{ backgroundImage: 'url(' + riverActualBg + ')' }">
      <img class="house-0-big-fishbird" src="@/assets/images/river/river-float-icon.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">旭川河</div>
        <div class="house-0-frame">
          <div>我想起來了，全都想起來了。沒錯，我的名字正是旭川河。我不像其他的河一樣是自然母親的孩子，我是人類開挖出來的運河，為的是服務人類的各種需要⋯⋯但是當我不被需要後，人類就將我掩蓋，抹煞我的存在，甚至將糞便尿液都倒到我身上。我的怨氣累積，使我成為了怪奇⋯⋯</div>
          <div @click="goToStepQ1115" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q11-15 -->
    <div v-if="step===11.15" class="house-0-box" :style="{ backgroundImage: 'url(' + riverActualBg + ')' }">
      <img class="house-0-big-fishbird" src="@/assets/images/river/river-float-icon.png" alt="ghost">
      <div class="house-0-all-box">
        <div @click="goToStepQ112" class="house-0-all-btn">呃⋯⋯我趕時間，講古的部分可以省略嗎？</div>
        <div @click="goToStepQ112" class="house-0-all-btn">這真是十分遺憾吶⋯⋯</div>
      </div>
    </div>
    <!-- Q11-2 -->
    <div v-if="step===11.2" class="house-0-box" :style="{ backgroundImage: 'url(' + riverActualBg + ')' }">
      <img class="house-0-big-fishbird" src="@/assets/images/river/river-float-icon.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">旭川河</div>
        <div class="house-0-frame">
          <div>感謝你，好人。你幫助我找回我的名字還有記憶，現在我必須告訴你一個真相。張中元⋯⋯你的館長，他並沒有告訴你捕捉怪奇的真正目的，你和我都是被他利用的棋子而已。不過不用擔心，我們已經想到辦法了，你就先照他的指示，搜集所有的怪奇，帶回去給他吧。<br>回去前，可以到「曙・初見咖啡」坐坐，只要跟店員說「我打贏旭川了」就可以獲得神秘禮物哦，快去吧！</div>
          <div @click="goToStepQ100" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- END 1 -->
    <div @click="goToStepQ1001" v-if="step===100" class="house-0-box" :style="{ backgroundImage: 'url(' + riverActualBg + ')' }">
      <div class="house-0-talk-box">
        <div class="house-0-float-frame">
          <div class="house-0-float-text" style="margin-top: 40vh;">怪奇旭川河在你眼前消失。你收服了他，你所在的建築物突然一陣搖晃，接著你聽到像是浴缸裡的水流掉的聲音，最後聲音歸於平靜。原本難以忍受的惡臭也消失，附近的市民一個個甦醒過來。</div>
        </div>
      </div>
    </div>
    <!-- END1-1 -->
    <div v-if="step==100.1" class="house-0-box" :style="{ backgroundImage: 'url(' + riverActualBg + ')' }">
      <div class="house-0-talk-box">
        <div class="house-0-last-frame">
          <div>本地區的怪力密度下降，人們也恢復正常狀態。</div>
        </div>
      </div>
    </div>
    <!-- last -->
  </div>
</template>

<script>

import silverBg from '@/assets/images/river/silver-shine-bg.png'
import riverActualBg from '@/assets/images/river/river-actual-bg.png'
// import riverOpacityBg from '@/assets/images/river/river-opacity-bg.png'
export default {
  name: 'RiverHouseEnd',
  components: {

  },
  data () {
    return {
      step: 10,
      riverName: '',
      peaceName: '',
      silverBg: silverBg,
      riverActualBg: riverActualBg
    }
  },
  mounted () {

  },
  methods: {
    answerRiverName () {
      if (this.riverName === '旭川河') {
        alert('答對了')
        this.step = 10.5
      } else {
        alert('名稱錯誤，無法收服怪奇。')
      }
    },
    answerPeaceName () {
      if (this.peaceName.toLowerCase() === 'adbcef' || this.peaceName.toLowerCase() === 'adbecf') {
        alert('答對了')
        this.step = 11
      } else {
        alert('名稱錯誤，無法收服怪奇。')
      }
    },
    goToStepQ111 () {
      this.step = 11.1
    },
    goToStepQ1115 () {
      this.step = 11.15
    },
    goToStepQ112 () {
      this.step = 11.2
    },
    goToStepQ100 () {
      this.step = 100
    },
    goToStepQ1001 () {
      this.step = 100.1
      this.$store.commit('changeHeader', true)
      this.$store.commit('changeNoteDot', true)
      this.$store.commit('changeNotDotHead', true)
      this.$store.commit('changeFrameDot', true)
      this.$store.commit('riverWin', true)
      this.$store.commit('getRiverCard', 'color')
      // 塞入 local storage
      let vuesList = JSON.parse(localStorage.getItem('vuexList'))
      vuesList.isRiverWin = true
      vuesList.riverCardType = 'color'
      localStorage.setItem('vuexList', JSON.stringify(vuesList))
      //
    }
  },
  computed: {
    power () {
      return this.$store.state.power
    }
  }
}
</script>
<style lang="scss" scoped>

  .river-house {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;

    .house-0 {

      &-box {
        position: relative;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-image: url('~@/assets/images/river/river-weto-bg.png');
      }

      &-fishbird {
        width: 48%;
        height: auto;
        position: absolute;
        top: 4vh;
      }

      &-talk-box {
        display: flex;
        flex-direction: column;
      }

      &-head {
        padding: 4px 16px;
        margin: 220px auto 0px;
        text-align: center;
        background: #95ff00;
        color: black;
        border-radius: 30px;
        z-index: 1;
      }

      &-frame {
        margin-top: 10px;
        max-height: 160px;
        padding: 20px;
        color: white;
        overflow: scroll;
        color: black;
        background: #95ff00;
        z-index: 1;
      }

      &-triangle-box {
        margin-top: 16px;
      }

      &-triangle {
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 15px 15px 0 15px;
        border-color: black transparent transparent transparent;
      }

      // black btn

      &-all-box {
        position: absolute;
        width: 100vw;
        height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-color: rgba(0, 0, 0, 0.8);
        padding-top: 25vh;
        z-index: 1;
      }

      &-all-btn {
        width: 100%;
        color: white;
        text-align: center;
        background: gray;
        margin-top: 30px;
        padding: 20px 10px;
      }

      // Q8

      &-big-fishbird {
        width: 100%;
        height: auto;
        position: absolute;
      }

      &-text {
        color: #95ff00;
        text-align: center;
      }

      &-all-box-quick {
        position: absolute;
        width: 100vw;
        height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-color: rgba(0, 0, 0, 0.8);
        padding-top: 15vh;
        z-index: 1;
      }

      // Q245

      &-all-245-box {
        width: 100vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 5vh;
        background-color: rgba(0, 0, 0, 0.8);
        color: white;
        z-index: 1;
      }

      &-245-fishbird {
        width: 30%;
        height: auto;
      }

      &-245-text {
      margin-top: 18vh;
      padding: 16px;
      }

      &-245-input {
        width: 150px;
        padding: 4px;
        color: black;
        margin-top: 10px;
      }

      &-245-go {
        background-color: #95ff00;
        color: black;
        padding: 6px;
        border-radius: 50%;
        text-align: center;
        margin-top: 10px;
      }

      &-last-frame {
        margin-top: 30vh;
        max-height: 200px;
        overflow: scroll;
        color: white;
        z-index: 1;
        padding: 20px;
      }

      // float

      &-float-frame {
        text-align: center;
        margin-top: -50px;
      }

      &-float-man {
        width: 40%;
      }

      &-float-text {
        text-align: left;
        padding: 0px 8px;
        margin-top: -20px;
        color: white;
      }

    }

  }

</style>



